import common from'../../util/common.js'
export default {
	data() {
		return {
			pwdV:''//密码
		}
	},
	onLoad() {
		// uni.setStorageSync("pageState","9");
	},
	// onShow() {
	// 	uni.setStorageSync("pageState","9")	
	// },
	methods: {
		//确定
		savePwd:function(){
			var examId = uni.getStorageSync("examIdS");
			if (examId == "" || examId == undefined || examId == null || examId == NaN || examId == 'NaN') {
				uni.showToast({
					title: "缺少考试ID请重新加入考试",
					icon: 'none',
					duration: 2000
				},function(){
					uni.reLaunch({
						url:'../index/index?mark =' + uni.getStorageSync("markS")
					})	
				})
				return
				
			}else{
				if(this.pwdV == ""){
					uni.showToast({
						title: "请输入考试密码",
						icon: 'none',
						duration: 2000
					})
					return
				}else{
					uni.request({
						url: common.port.mVerify,
						header: {
							'content-type': 'application/x-www-form-urlencoded',
						},
						method: "POST",
						dataType: "json",
						data:{
							exam_id: examId,
							password: this.pwdV,
						},
						success: (result) => {
							console.log(result.data);
							if (result.data.code == 0) {
								uni.setStorageSync("pwdstartS","1")
								uni.redirectTo({
									url: '../notice/notice'
								})
							} else {
								uni.showToast({
									title: result.data.msg,
									icon: 'none',
									duration: 2000
								})
								return
							}
							
						}
						
					})
				}
			}
		}
	}
}
